  .coin{
      display: flex;
      align-items: center;
      padding-right: 24px;
      min-width: 300px;
  }

  .coin-container{
      display: flex;
      justify-content: center;
  }

  .coin-row {
      display: flex;
      flex-direction: row;
      justify-items: start;
      align-items: center;
      height: 80px;
      border-bottom: 1px solid #d7d7d7;
      width: 900px;
  }

  .coin img{
      height: 30px;
      width: 30px;
      margin-right: 10px;
  }

  .coin h1{
      font-size: 16px;
      width: 150px;
  }

  .coin-symbol{
      text-transform: uppercase;
  }

  .coin-data{
      display: flex;
      text-align: center;
      justify-content: space-between;
      width: 100%;
  }

  .coin-price{
      width: 110px;
  }
  .coin-volume{
    width: 200px;

  }

  .coin-marketcap{
    width: 240px;

  }

  .coin-percent{
      width: 88px;
  }
  .red{
      color: #f00606;
  }
  .green{
      color: #11d811;
  }




@media (max-width: 991.98px) { 

    .coin{
        padding-right: 16px;
        min-width: 80px;
    }

    .coin-row {
        height: 80px;
        width: 700px;
    }
  
  
    .coin-price{
        width: 60px;  
}
.coin-volume{
    width: 130px;
  }
  .coin-marketcap{
    width: 150px;

  }

  .coin-percent{
      width: 58px;
  }

  .coin-data{
    min-width: 70%;

  }
  .coin img{
    height: 20px;
    width: 20px;
    margin-right: 6px;
}

.coin h1{
    font-size: 16px;
    width: 60px;
}

}


@media (max-width: 575.98px) {

    .coin{
        padding-right: 3px;
        min-width: 70px;
    }

    .coin-row {
        height: 70px;
        min-width: 310px;
    }
 .coin p {
     font-size: 6px;
 }

    .coin-price{
        width: 60px;  
}

.coin h1{
    font-size: 16px;
    width: 60px;
}

.coin-volume{
    width: 90px;
  }
  .coin-marketcap{
    width: 80px;

  }

  .coin-percent{
      width: 38px;
  }

}
* {
    box-sizing: border-box;
    margin: 0;
   background-color: #1a1a1c;
   font-family: 'Baloo Bhai 2', cursive;
   color: #fff;

  }
  
  .coin-app{
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 64px;
      color: #fff;
  }
  .coin-search{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 64px;
    justify-content: center;

  }

  .coin-text {
      margin-bottom: 32px;
      text-align: center;
  }

  .coin-input{
      padding-left: 16px;
      width: 300px;
      height: 50px;
      border-radius: 4px;
      border: none;
      background-image: linear-gradient(
          -225deg,
          #ac32e4 0%,
          #7918f2 48%,
          #4801ff 100%
      );
      color: #e2e2e2;
  }
  .coin-input::-webkit-input-placeholder{
   color: #e2e2e2;
  }
  .coin-input:-ms-input-placeholder{
   color: #e2e2e2;
  }
  .coin-input::-ms-input-placeholder{
   color: #e2e2e2;
  }
  .coin-input::placeholder{
   color: #e2e2e2;
  }
